import Vue from 'vue';

import Vuex from 'vuex';

import home from './modules/home'
import types from './modules/types'
import search from './modules/search'


Vue.use(Vuex);

export default new Vuex.Store ({
    modules:{
        home,
        types,
        search
    }
})


