<template>
    <div class="lfet_box">
        <div class="tab_item" 
            v-for="(item,i) in LfetTabs" 
            :key="'ltab'+i" 
            @click="sendRightDatas(item.maitKey,item.miniWallkey)"
            :class="{active:item.maitKey==isActive}">
            <p class="item_p">
               {{ item.title }} 
            </p>
        </div>
    </div>
</template>
  
<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  props: {
    LfetTabs: { type: Array, defaute: [] }
  },
  computed: {
    ...mapState(["types"])
  },
  methods: {
    ...mapActions(["loadTypesByRightDatas","loadeLikesByRightDatas"]),
    sendRightDatas(pid,fcid) {
      this.loadTypesByRightDatas(pid);
      this.isActive = pid;
      this.loadeLikesByRightDatas([fcid,"pop"])
    }
  },
  mounted() {
    this.loadTypesByRightDatas(3627);
  },
  data() {
    return {
      isActive: 3627
    };
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.lfet_box {
  > .tab_item {
    position: relative;
    width: 100%;
    height: 90px;
    line-height: 90px;
    text-align: center;
    font-size: 30px;
    color: #666666;
    background: #f8f9fa;
    > .item_p {
      position: absolute;
      top: 30%;
      width: 80%;
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      border-left: 5px solid #f8f9fa;
    }
  }
  >.active {
    background-color: white;
    > p {
      border-left: 5px solid #ff5577 !important;
      font-weight: bold;
      color: #ff5577;
    }
  }
}
</style>


