<template>
    <div class="search-box">
        <div class="search">
            <div class="search_item">
                <div class="search_input">
                    <input type="text" :placeholder="JSON.parse($route.query.params).q">
                    <a href="javascript:;" class="search_a"></a>
                </div>    
                <!-- search.KeyWord -->
                <i class="iconfont icon-gouwuche"></i>
            </div>
            <div class="wall_nav_box">
                <div 
                    class="nav_item" 
                    v-for="(tabs,j) in search.SearchSortFilter" 
                    :key="'tabs'+j" 
                    @click="seadSearch($route.query.params,search.SearchSortFilter[j].sortKey)"
                    :class="{active:search.SearchSortFilter[j].sortKey==isActive}">
                    <p :class="'tabs_p'+j">{{ tabs.title }}</p>
                </div>
                <div class="nav_item">
                    <p class="tabs_p3">
                      <img src="../../static/img/xiangxia.png" alt="">
                    </p>
                </div>
            </div>
        </div>
        <scroll-view class="wrapper">
            <div class="sac_module">
                <div class="wall_like_box">
                    <div class="like_item" v-for="(likes,k) in search.SearchDocs" :key="'like'+k" @click="seadItems(likes)">
                        <div class="like_img">
                            <img :src="likes.img" alt="">
                        </div>
                        <div class="like_title">{{ likes.title }}</div>
                        <div class="like_res">
                            <div class="like_price"> {{ likes.price }}</div>
                            <div class="like_cfav" >
                                {{ likes.cfav }}
                                <i class="iconfont icon-shoucang" @click="showSc()"></i> 
                            </div>
                        </div> 
                    </div>
                    <div></div>
                </div>
            </div>
        </scroll-view>
        
    </div>
</template>
    
<script>
import { mapState, mapActions, mapMutations } from "vuex";
// import ScrollView from "../globalComponents/ScrollView";
export default {
  data() {
    return {
      isActive: "pop"
    };
  },
  components: {
    // ScrollView
  },
  computed: {
    ...mapState(["search"])
  },
  methods: {
    ...mapActions(["loadeDataByGoods"]),
    seadSearch(params, sorts) {
      this.isActive = sorts;
      console.log(params, sorts);
      let obj = JSON.parse(params);
      // console.log(obj)
      for (var key in obj) {
        if (key === "sort") {
          obj[key] = sorts;
        }
      }
      console.log(obj);
      this.loadeDataByGoods(obj);
    },
    seadItems(items) {
      this.$router.push({ path: "/product", query: { items } });
    }
  },
  mounted() {
    let params = this.$route.query.params;
    this.loadeDataByGoods(JSON.parse(params));
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.search-box {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: #fff;
  z-index:101;
  > .search {
    z-index: 1;
    background-color: white;
    padding: 0px 5px;
    > .search_item {
      padding: 10px 10px;
      height: 60px;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;
      > .search_input {
        flex-grow: 7;
        border-radius: 6px;
        margin-left: 20px;
        padding-left: 60px;
        background: #eee url("../../static/img/suosuo.png") no-repeat 15px
          center;
        > input {
          border: none;
          font-size: 32px;
          color: #999;
          line-height: 60px;
          background: #eee;
        }
        > .search_a {
          width: 60px;
          height: 60px;
          float: right;
          background: url("../../static/img/chehui.png") no-repeat 10px center;
          background-size: 48px auto;
          padding-left: 10px;
        }
      }
      > i {
        flex-grow: 2;
        text-align: center;
        line-height: 60px;
        font-size: 48px;
        color: #727272;
      }
    }
    > .wall_nav_box {
      display: flex;
      height: 90px;
      line-height: 40px;
      border-top: @border #ccc;
      border-bottom: @border #ccc;
      > .nav_item {
        width: 25%;
        height: 100%;
        text-align: center;
        > p {
          width: 100%;
          display: inline-block;
          height: 32px;
          font-size: 30px;
          margin-top: 25px;
        }
      }
      .active {
        > p {
          color: #ff557f;
        }
      }
      .tabs_p0 {
        border-right: @border #ccc;
      }
      .tabs_p2 {
        border-left: @border #ccc;
        border-right: @border #ccc;
      }
      .tabs_p3 {
        > img {
          width: 32px;
        }
      }
    }
  }
  >.wrapper {
    overflow: hidden;
    position: absolute;
    top:190px;
    bottom:0px;
    .content {
      .sac_module {
          width:100%;
        > .wall_like_box { 
          display: flex;
          flex-flow: wrap;
          margin-top: 15px;
          justify-content: space-around;
          > div {
            width: 45%;
          }
          > .like_item {
            width: 45%;
            height: 610px;
            > .like_img {
              width: 100%;
              height: 450px;
              > img {
                height: 100%;
                width: 100%;
                border-radius: 5px;
              }
            }
            > .like_title {
              width: 100%;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              text-indent: 1em;
              margin-top: 15px;
              position: relative; 
              line-height: 35px; 
              max-height: 70px;
              font-size:27px;
              overflow: hidden;
            }
            .like_title::after{
              content: "..."; 
              position: absolute; 
              bottom: 0; 
              right: 0; 
              padding-left: 40px;
              background: -webkit-linear-gradient(left, transparent, #fff 55%);
              background: -o-linear-gradient(right, transparent, #fff 55%);
              background: -moz-linear-gradient(right, transparent, #fff 55%);
              background: linear-gradient(to right, transparent, #fff 55%);
            }
            > .like_res {
              display: flex;
              justify-content: space-between;
              margin-top: 20px;
              > .like_price {
                font-size: 30px;
                font-weight: bold;
              }
              > .like_cfav {
                color: #999;
                font-size: 23px;
              }
            }
          }
        }
      }
    }
  }
}
</style>


