<template>
    <div class="mall-box">
        <div class="mall_res">
            <div class="res_item" v-for="(item,i) of resItem " :key="'res'+i">
                <div class="item_img">
                    <img :src="item.image" alt="">
                    <span>{{item.title}}</span>
                </div>    
            </div>
        </div>
    </div>
</template>
    
<script>
export default {
  props: {
    resItem: { type: Array, defaute: [] }
  },
  data() {
    return {
      
    };
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.mall {
  &-box {
    >.mall_res {
      display: flex;
      flex-flow: row wrap;
      margin-right:20px;
      >.res_item {
        width: 20%;
        text-align: center;
        >.item_img{
            margin-left:20px;
            margin-top: 25px;
            >img{
                width:100%;
                height:auto;
            }
        }
      }
    }
  }
}
</style>


