<template>
    <div class="right-box">
        <div class="right_types">
            <div v-for="(item,i) in RightTabs" 
                :key="'rtab'+i" 
                class="right_items"
                @click="rightSearch(item.link,item.title)">
                <div class="right_img">
                    <img :src="item.image" alt="">    
                </div>  
                <p>{{ item.title }}</p>
            </div>
        </div>

        <div class="wall_module">
            <div class="wall_nav_box">
                <div 
                    class="nav_item" 
                    v-for="(tabs,j) in types.RightSortFilter" 
                    :key="'tabs'+j" 
                    @click="sendLikes([types.RightFcid,tabs.sortKey])"
                    :class="{active:tabs.sortKey==isActive}">
                    <p :class="'tabs_p'+j">{{ tabs.title }}</p>
                </div>
            </div>
            <div class="wall_like_box">
                <div class="like_item" v-for="(likes,k) in types.RightLikes" :key="'like'+k" @click="seadLike(likes)">
                    <div class="like_img">
                        <img :src="likes.img" alt="">
                    </div>
                    <div class="like_title">{{ likes.title }}</div>
                    <div class="like_res">
                        <div class="like_price"> {{ likes.price }}</div>
                        <div class="like_cfav" >
                            {{ likes.cfav }}
                            <i class="iconfont icon-shoucang" @click="showSc()"></i> 
                        </div>
                    </div> 
                </div>
                <div></div>
            </div>
        </div>
    </div>
</template>
    
<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    return {
      isActive: "pop"
    };
  },
  props: {
    RightTabs: { type: Array, defaute: [] }
  },
  computed: {
    ...mapState(["types"])
  },
  methods: {
    ...mapActions(["loadTypesByRightDatas", "loadeLikesByRightDatas"]),
    sendLikes([fcid, sort]) {
      // console.log(sort);
      this.loadeLikesByRightDatas([fcid, sort + ""]);
      this.isActive = sort;
    },
    seadLike(items) {
      this.$router.push({ path: "/product", query: { items } });
    },
    rightSearch(link, title) {
      // var reg=/(?<=(fcid\=))\S*(?=(&title))/;
      // var regs=reg.exec(link);
      // var fcid=regs[0]
      var titles = link.indexOf("=");
      var titles1 = link.indexOf("&");
      var fcid = link.slice(titles + 1, titles1);
      console.log(fcid);
      console.log(fcid, title);
      let params = {
        fcid,
        title,
        cKey: 16,
        sort: "pop",
        page: 1
      };
      this.$router.push("/search?params=" + JSON.stringify(params));
    }
  },
  mounted() {
    this.loadeLikesByRightDatas([10062603, "pop"]);
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.right-box {
  width: 100%;
  padding: 30px 0px 0px;
  .right_types {
    display: flex;
    flex-flow: row wrap;
    text-align: center;
    .right_items {
      width: 33.33%;
      height: 200px;
      margin: 15px 0;
      overflow: hidden;
      > .right_img {
        width: 70%;
        height: 140px;
        margin: 0px auto 15px;
        > img {
          width: 100%;
          height: 100%;
        }
      }
      > p {
        font-size: 24px;
      }
    }
  }
  > .wall_module {
    width: 100%;
    > .wall_nav_box {
      display: flex;
      height: 90px;
      line-height: 40px;
      border-top: @border #ccc;
      border-bottom: @border #ccc;
      > .nav_item {
        width: 33.33%;
        height: 100%;
        text-align: center;
        > p {
          width: 100%;
          display: inline-block;
          height: 32px;
          font-size: 30px;
          margin-top: 25px;
        }
      }
      .active{
        >p{
          color: #ff557f;
        }
      }
      .tabs_p0 {
        border-right: @border #ccc;
      }
      .tabs_p2 {
        border-left: @border #ccc;
      }
    }
    > .wall_like_box {
      display: flex;
      flex-flow: wrap;
      margin-top: 15px;
      justify-content: space-around;
      > div {
        width: 45%;
      }
      > .like_item {
        width: 45%;
        height: 480px;
        > .like_img {
          width: 100%;
          height: 360px;
          > img {
            height: 100%;
            width: 100%;
            border-radius: 5px;
          }
        }
        > .like_title {
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          text-indent: 1em;
          margin-top: 15px;
          position: relative; 
          line-height: 35px; 
          max-height: 70px;
          overflow: hidden;
          font-size:26px;
          }
          .like_title::after{
            content: "..."; 
            position: absolute;
            bottom: 0; 
            right: 0; 
            padding-left: 40px;
            background: -webkit-linear-gradient(left, transparent, #fff 55%);
            background: -o-linear-gradient(right, transparent, #fff 55%);
            background: -moz-linear-gradient(right, transparent, #fff 55%);
            background: linear-gradient(to right, transparent, #fff 55%);
          }
        > .like_res {
          display: flex;
          justify-content: space-between;
          margin-top: 15px;
          > .like_price {
            font-size: 28px;
            font-weight: bold;
            color:#ff557f;
          }
          > .like_cfav {
              color: #999;
            font-size: 23px;
          }
        }
      }
    }
  }
}
</style>


