import api from '../../server/api'

const state={
    LfetTabs:[],
    RightTabs:[],
    RightSortFilter:[],
    RightLikes:[],
    RightFcid:{}
}

const actions={
    loadTypesDatas({commit}){
        api.loadeDataByLife({
            pids:"117330"
        },(data)=>{
            commit("setLfetTabs",data.data["117330"].list);
        });
    },
    loadTypesByRightDatas({commit},pid){
        api.loadeDataByRight(pid,(data)=>{
            commit("setRightTabs",data.data.list)
        })
    },
    loadeLikesByRightDatas({commit},ficdParams){
        api.loadeDataByRightLike(ficdParams,(data)=>{
            commit("setRightLikes",data.result.wall.docs);
            commit("setRightSortFilter",data.result.sortFilter);
            commit("setRightFcid",data.result.param.fcid);
        })
    }
    
}

const mutations={
    setLfetTabs(state,data){
        state.LfetTabs=data;
    },
    setRightTabs(state,data){
        state.RightTabs=data;
    },
    setRightSortFilter(state,data){
        state.RightSortFilter=data;
    },
    setRightLikes(state,data){
        state.RightLikes=data;
    },
    setRightFcid(state,data){
        state.RightFcid=data;
    }
}

export default {
    state,
    actions,
    mutations,
}


