<template>
    <div class="hot-box">
        <div class="h_title">
            <p><span>&nbsp;</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>&nbsp;</span></p>
        </div>
        <div class="hot_item">
            <div class="item_res" v-for="(item,i) of hotTypes " :key="'hot'+i">
                <div class="hot_res">
                    <img :src="item.image" alt="">
                    <span>{{ item.title }}</span>
                </div>
            </div>    
        </div>
    </div>
</template>
    
<script>
export default {
  props:{
    hotTypes:{type:Array, defaute:[]}
  },
  data() {
    return {
    
    };
  },
  components: {}
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.hot {
  &-box {
    margin-bottom: 10px;
    > .h_title {
      text-align: center;
      font-size: 32px;
      margin-top: 30px;
      margin-bottom: 30px;
    }
    > .hot_item {
      display: flex;
      flex-flow:  wrap;
      margin-right: 20px;
      > .item_res {
        width: 25%;
        text-align: center;
        >.hot_res{
            margin-left:20px;
            margin-top: 25px;
            >img{
                width:100%;
                height:auto;
            }
        }
      }
    }
  }
}
</style>


