import api from '../../server/api'

const state = {
    swiperSlides: [],
    resItem: [],
    benefitPromotion: [],
    hotTypes: [],
    guessLike: []
}

const actions = {
    loadHomeDatas({commit}) {
        api.loadeDataByPids([122224, 106930, 51833, 51836], (data) => {
            commit("setSwiperSlides",data.data["122224"].list);
            commit("setResItem",data.data["106930"].list);
            commit("setBenefitPromotion",data.data["51833"].list);
            commit("setHotTypes",data.data["51836"].list);
        });
        api.loadeDataByLike({
            pid:"9750",
            sort:"pop",
            cKey:46
        },(data) => {
            commit("setGuessLike",data.result.wall.docs)
        })
    },
}

const mutations = {
    setSwiperSlides(state,data){
        state.swiperSlides=data;
    },
    setResItem(state,data){
        state.resItem=data;
    },
    setBenefitPromotion(state,data){
        state.benefitPromotion=data;
    },
    setHotTypes(state,data){
        state.hotTypes=data;
    },
    setGuessLike(state,data){
        state.guessLike=data;
    },
}

export default {
    state,
    actions,
    mutations,
}


