import jsonpUtil from '../util/jsonpUtil'

const mce_multiget = "https://mce.mogucdn.com/jsonp/multiget/3";
const list_seach = "https://list.mogujie.com/search";
const mce_get = "https://mce.mogucdn.com/jsonp/get/3";
const list_mget="https://list.mogujie.com/module/mget";
const rate_ratelist="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";

const pidsParams = {
    appPlat: "m"
}
const fcidParams = {
    page: "1",
    cKey: "h5-cube"
}
const hsParams = {
    platform:"H5",
    code:"hotWord,sketch"
}

var arr=[].slice.call(fcidParams);

const error = function (error) {
    alert(error.message);
}

export default {
    loadeDataByPids(pids, success = () => { }) {
        if (!pids) {
            throw new Error("");
        }
        if (!Array.isArray(pids)) {
            pidsParams.pids = pids;
        }
        if (Array.isArray(pids)) {
            let temp = "";
            for (const i in pids) {
                temp += pids[i] + ",";
            }
            pidsParams.pids = temp.replace(/,$/, "");
        }
        jsonpUtil.request(mce_multiget, pidsParams)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("");
                }

            })
            .then(success)
            .catch(error)
    },
    loadeDataByLike(params, success = () => { }) {
        jsonpUtil.request(list_seach, params)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("");
                }

            })
            .then(success)
            .catch(error)
    },
    loadeDataByLife(pids, success = () => { }) {
        jsonpUtil.request(mce_multiget, pids)
            .then((response) => {
                console.log(response)
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("");
                }

            })
            .then(success)
            .catch(error)
    },
    loadeDataByRight(pid, success = () => { }) {
        jsonpUtil.request(mce_get, { pid, appPlat: "m" })
            .then((response) => {
                console.log(response)
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("");
                }

            })
            .then(success)
            .catch(error)
    },
    loadeDataByRightLike(arr,success=()=>{}){
        if (Array.isArray(arr)) {
            fcidParams.fcid = arr[0];
            fcidParams.sort = arr[1];
        }
        jsonpUtil.request(list_seach,fcidParams)
                .then((response)=>{
                    // console.log(response)
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("");
                    }

                })
                .then(success)
                .catch(error)
    },
    loadeDataByRightSearch({fcid,title,sort},success=()=>{}){
        jsonpUtil.request(list_seach,{
            fcid,
            title,
            cKey:"16",
            sort,
            page:"1",
        })
        .then((response)=>{
            if(response.ok){
                return response.json();
            }else{
                throw new Error("");
            }
        })
        .then(success)
        .catch(error)
    },
    loadeDataByKeyWord(success=()=>{}){
        jsonpUtil.request(list_mget,hsParams)
                .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("");
                    }
                })
                .then(success)
                .catch(error)
    },
    
    loadeDataByGoods(params,success=()=>{}){
        jsonpUtil.request(list_seach,params)
        .then((response)=>{
            if(response.ok){
                return response.json();
            }else{
                throw new Error("");
            }
        })
        .then(success)
        .catch(error)
    },
    loadeDataByComment(itemId,success=()=>{}){
        jsonpUtil.request(rate_ratelist,itemId)
        .then((response)=>{
            if(response.ok){
                return response.json();
            }else{
                throw new Error("");
            }
        })
        .then(success)
        .catch(error)
    }
}


