<template>
    <div class="bottom-box">
        <router-link tag="div" to="/home" class="bottom-item">
            <i class="icon iconfont icon-shouye"></i>
            <span></span>
        </router-link>
        <router-link tag="div" to="/types" class="bottom-item">
            <i class="icon iconfont icon-fenlei"></i>
            <span></span>
        </router-link>
        <router-link tag="div" to="/cart" class="bottom-item">
            <i class="icon iconfont icon-gouwuche"></i>
            <span></span>
        </router-link>
        <router-link tag="div" to="/me" class="bottom-item">
            <i class="icon iconfont icon-tubiaolunkuo-"></i>
            <span></span>
        </router-link>
    </div>
</template>
    
<script>
export default {
  methods: {
  } 
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.bottom {
  &-box {
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    display: flex;
    height: 98px;
    flex-flow: row nowrap;
    justify-content: center;
    background: white;
    border-top: @border #ccc; /*no*/
    .bottom-item {
      flex-grow: 1;
      text-align: center;
      height: 100%;
      > i {
        display: block;
        font-size: 46px; 
        color: #666;
        margin-top: 10px;
      }
      > span {
        display: block;
        font-size: 24px;
        color: #666;
        margin: 2px 0px;
      }
    }
  }
}

.active>.icon,.active>span{
  color:#FF5777!important;
  
}

</style>


