<template>
    <div class="swiper-box"> 
        <swiper :options="swiperOption">
            <swiper-slide v-for="(slide,i) in swiperSlides" :key="'swiper'+i">
              <img :src="slide.image_800">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>
    
<script>
export default {
  name: "carrousel",
  props: {
    swiperSlides: { type: Array}
  },
  data() {
    return {
      swiperOption: {
        autoplay:500,
        // delay:1000,
        // speed: 1000,
        loop: true,
        pagination:".swiper-pagination"
        
      }
    };
  },
  computed: {},
  mounted() {}
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.swiper {
  &-box {
    height: 300px;
    overflow-y:hidden;
    img {
      width: 100%;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
      bottom: 20px;
      left: 0;
      width: 100%;
      .swiper-pagination-bullet {
        width: 15px;
        height: 15px;
        display: inline-block;
        border-radius: 100%;
        background: #000;
        opacity: 0.2;
      }
      .swiper-pagination-bullet-active {
        opacity: 1;
        background: #007aff;
      }
    }
  }
}
</style>


