<template>
    <div class="benefit-box">
        <div class="b_title">
            <p><span>&nbsp;&nbsp;</span>&nbsp;&nbsp;-&nbsp;&nbsp;<span>&nbsp;&nbsp;</span></p>
        </div>
        <div class="b_conent">
            <div :class="'c_promotion promotion'+i" v-for="(item,i) in benefitPromotion" :key="'benefit'+i">
                <div class="promotion_p">
                    <p class="promotion_p1">{{ item.title }}</p>
                    <p class="promotion_p2">{{ item.viceTitle.substring(1,item.viceTitle.length-1) }}</p>
                </div>
                <img :src="item.image" alt="">
            </div>
        </div>
    </div>
</template>
    
<script>
export default {
  props:{
    benefitPromotion:{type:Array, defaute:[]}
  },
  data() {
    return {
      
    };
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.benefit {
  &-box {
    margin-bottom: 10px;
    > .b_title {
      text-align: center;
      font-size: 32px;
      margin-top: 30px;
      margin-bottom: 30px;
    }
    .b_conent {
      display: flex;
      flex-flow: wrap;
      position: relative;
      > .c_promotion {
        text-align: center;
        border: @border #ccc;
        box-sizing: border-box;
        > .promotion_p {
          > .promotion_p1 {
            font-size: 30px;
          }
          > .promotion_p2 {
            color: #ff5f7e;
          }
        }
      }
      > .promotion0 {
        width: 50%;
        height: 320px;
        >.promotion_p{
            margin-top: 30px;
            height:20%;
            margin-bottom:15px;
        }
        >img{
            margin-top: 10px;
            height:60%;
        }
      }
      > .promotion1 {
        width: 50%;
        height: 160px;
        display: flex;
        flex-flow:nowrap;
        >.promotion_p{ 
            width:75%;  
            text-align:left;
            margin-top:40px;
            margin-left:15px;
        }
        > img {
            width:40%;
            height: auto;
        }
      }
      > .promotion2 {
        width: 50%;
        height: 160px;
        position: absolute;
        top: 160px;
        left: 50%;
        display: flex;
        flex-flow:nowrap;
        >.promotion_p{ 
            width:65%;  
            text-align:left;
            margin-top:40px;
            margin-left:15px;
        }
        > img {
            width:35%;
            height: auto;
        }
      }
      > .promotion3,
      .promotion4,
      .promotion5 {
        width: 33.33%;
        height:220px;
        >.promotion_p{
            margin-top:20px;
        }
        >img{
            height:50%;
        }
      }
    }
  }
}
</style>


