import api from '../../server/api'

const state={
    KeyWord:[],
    HotKeyWord:[],
    SearchSortFilter:[],
    SearchDocs:[],
    Comments:[],
    RateScore:[]

}

const actions={
    loadeTopByKeyWord({commit}){
        api.loadeDataByKeyWord((data)=>{
            commit("setKeyWord",data.data.sketch.data.frontword);
            commit("setHotKeyWord",data.data.hotWord.data);
        });
    },
    loadeDataByGoods({commit},params){
        commit("setSearchDocs",[]);
        api.loadeDataByGoods(params,(data)=>{
            commit("setSearchSortFilter",data.result.sortFilter);
            commit("setSearchDocs",data.result.wall.docs)
        })
    },
    loadeDataByComments({commit},itemId){
        api.loadeDataByComment(itemId,(data)=>{
            commit("setIComments",data.data.list);
            commit("setRateScore",data.data.rateScore)
        })
    }
}


const mutations={
    setKeyWord(state,data){
        state.KeyWord=data;
    },
    setHotKeyWord(state,data){
        state.HotKeyWord=data;
    },
    setSearchSortFilter(state,data){
        state.SearchSortFilter=data;
    },
    setSearchDocs(state,data){
        state.SearchDocs=data;
    },
    setIComments(state,data){
        state.Comments=data;
    },
    setRateScore(state,data){
        state.RateScore=data;
    },
}

export default {
    state,
    actions,
    mutations
}


