<template>
    <div class="home-box">
      <scroll-view class="wrapper">
        <slide-show :swiperSlides="home.swiperSlides"></slide-show>
        <mall-nav :resItem="home.resItem"></mall-nav>
        <benefit-promotion :benefitPromotion="home.benefitPromotion"></benefit-promotion>
        <hot-types :hotTypes="home.hotTypes"></hot-types>
        <guess-you-like :guessLike="home.guessLike"></guess-you-like>
      </scroll-view> 
    </div>              
</template>
    
<script>
import SlideShow from "../components/SlideShow";
import ScrollView from '../globalComponents/ScrollView';
import MallNav from '../components/MallNav';
import BenefitPromotion from '../components/BenefitPromotion';
import HotTypes from '../components/HotTypes';
import GuessYouLike from '../components/GuessYouLike'

import { mapState,mapActions,mapMutations } from 'vuex'
export default {
  components: {
    SlideShow,
    ScrollView,
    MallNav,
    BenefitPromotion,
    HotTypes,
    GuessYouLike
  },

  computed:{
    ...mapState(["home"])
  },

  methods:{
    ...mapActions(["loadHomeDatas"])
  },
  mounted() {
    this.loadHomeDatas();
  },

};
</script>
    
<style lang="less">
@import url("../less/less-var");
.home-box{
  position: absolute;
  top: 92px;
  bottom: 98px;
  left: 0px;
  right: 0px;
}
.wrapper {
  height: 100%;
  overflow: hidden;
}
</style>


