<template>
    <div class="types-box">
        <div class="wrapper">
            <scroll-view class="lfet_tab">
                <lfet-tab :LfetTabs="types.LfetTabs"></lfet-tab>
            </scroll-view>
            <scroll-view class="right_tab">
                <right-tab :RightTabs="types.RightTabs"></right-tab>
            </scroll-view>
        </div>   
    </div>
</template>
  
<script>
    import LfetTab from '../components/LfetTab';
    import RightTab from '../components/RightTab';
    import ScrollView from '../globalComponents/ScrollView';

    import { mapState,mapActions,mapMutations } from 'vuex'
export default {
    computed:{
        ...mapState(["types"])
    },
    methods:{
    ...mapActions(["loadTypesDatas"]),
    },
    mounted() {
        this.loadTypesDatas();
    },
    components:{
        LfetTab,
        ScrollView,
        RightTab
    }
}
</script>
   
<style lang="less">
@import url("../less/less-var");
.types-box{
    position: absolute;
    top: 92px;
    bottom: 98px;
    left: 0px;
    right: 0px;
    >.wrapper{
        height: 100%;
        overflow: hidden;
        display: flex;
        >.lfet_tab{
            width:25%;
            height:100%;
        }
        >.right_tab{
            width:75%;
            height:100%;
        }
    }
}
    
</style>


