<template>
    <div class="nav-box">
        <div class="search">
            <i v-show="!flag" class='iconfont icon-xiaoxizhongxin'></i>
            <i v-show="flag" class='iconfont icon-xiangzuojiantou' @click="flag=!flag"></i>
            <input class="home_input" v-if="!flag" type="text" :placeholder="search.KeyWord" @focus="flag=true" v-model="userInput" />
            <input class="sec_input" v-if="flag" type="text" :placeholder="search.KeyWord" @focus="flag!=true" v-model="userInput" />
            <i v-show="!flag" class="iconfont icon-gouwuche"></i>
            <span v-show="flag" @click="seadGoods(userInput,searchs)"></span>
        </div>
        <div v-show="flag" class="search-content">
          <div class="sc_history">
            <div class="sc_his_head">
              <i class="iconfont icon-sousuo"></i>
              <span></span>
              <div class="iconfont icon-lajixiang"></div>
            </div>
            <div class="sc_his_items">
              <ul>
                <li v-for="(data,i) of searchs" :key="'data'+i">
                  {{ data }}
                </li>
              </ul>
            </div>
          </div>
          <div class="sc_hotwords">
            <div class="hot_head">
              <i class="iconfont icon-shoucang"></i>
              <span></span>
            </div>
            <div class="hot_items">
              <ul>
                <li v-for="(hots,i) in search.HotKeyWord" :key="'hots'+i">
                  <a @click="seadHot(hots.frontword)">{{ hots.frontword }}</a>
                </li>
              </ul>
            </div>
          </div>
        </div>
    </div>
</template>
    
<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    return {
      flag: false,
      userInput: "",
      searchs: []
    };
  },
  computed: {
    ...mapState(["search"])
  },
  methods: {
    ...mapActions(["loadeTopByKeyWord"]),
    seadGoods(q, s) {
      if (q == "") {
        q = this.search.KeyWord;
      }
      let params = {
        q,
        cKey: 46,
        page: 1,
        sort: "pop"
      };
      this.$router.push("/search?params=" + JSON.stringify(params));

      s.push(q);

      localStorage.setItem("searchs", s);
    },
    seadHot(q) {
      console.log(q);
      let hotParams = {
        q,
        cKey: 46,
        page: 1,
        sort: "pop"
      };
      this.$router.push("/search?params=" + JSON.stringify(hotParams));
    }
  },
  mounted() {
    this.loadeTopByKeyWord();

    let a = localStorage.getItem("searchs");

    if (a == null) {
      a = [];
    } else {
      a = a.split(",");
    }
    this.searchs = a;
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.nav {
  &-box {
    width: 100%;
    height: 92px;
    z-index: 100;
    > .search {
      position: fixed;
      top: 0px;
      right: 0px;
      left: 0px;
      padding: 15px 10px;
      height: 60px;
      border-bottom: @border #ccc; /*no*/
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;
      > i {
        flex-grow: 1;
        font-size: 50px;
        color: #999;
        text-align: center;
      }
      > .home_input {
        flex-grow: 5;
        border-radius: 6px;
        border: none;
        font-size: 24px;
        color: #999;
        padding-left: 60px;
        background: #eee url("../../static/img/suosuo.png") no-repeat 15px
          center;
        background-size: 31px auto;
      }
      > .sec_input {
        flex-grow: 20;
        padding: 10px 20px;
        float: left;
        margin-top: 3px;
        height: 30px;
        border-radius: 9px;
        border: @border #ff5577; /*no*/
        font-size: 28px;
        line-height: 30px;
        vertical-align: middle;
        background: transparent;
        background-color: #fff;
        outline: none;
      }
      > span {
        line-height: 60px;
        flex-grow: 1;
        text-align: center;
        height: 60px;
        width: 70px;
        font-size: 28px;
        color: #666;
      }
    }
    > .search-content {
      position: absolute;
      top: 92px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      background: white;
      z-index: 99;
      > .sc_history,
      .sc_hotwords {
        min-height: 90px;
        overflow: hidden;
        margin-top: 10px;
        padding: 20px;
        color: #999;
        > .sc_his_head,
        .hot_head {
          > i {
            height: 30px;
            width: 30px;
            float: left;
            margin-left: 10px;
            margin-top: 10px;
          }
          > span {
            font-size: 28px;
            line-height: 45px;
            height: 45px;
            margin-left: 10px;
          }
          > div {
            float: right;
          }
        }
      }
      .sc_his_items {
        // margin-top: 20px;
        > ul {
          list-style: none;
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;
          margin-top: 15px;
          > li {
            border: @border #ccc;
            width: 100px;
            height: 50px;
            border-radius: 8px;
            font-size: 28px;
            text-align: center;
            line-height: 50px;
            margin-left: 28px;
            margin-top: 32px;
            color: #666;
          }
        }
      }
    }
    .hot_items {
      > ul {
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        margin-top: 15px;
        > li {
          border: @border #ccc;
          width: 100px;
          height: 50px;
          border-radius: 8px;
          font-size: 28px;
          text-align: center;
          line-height: 50px;
          margin-left: 28px;
          margin-top: 32px;
          color: #666;
        }
      }
    }
  }
}
</style>


