import jsonp from 'fetch-jsonp'

const parseParamsObject = function(obj){
    let temp = "?";
    for (const key in obj) {
        temp +=`${key}=${obj[key]}&`;
    }
    return temp.replace(/&$/,"");
}

export default {
    request(url,paramsObj,opt={}){
        let queryParms = parseParamsObject(paramsObj);
        return jsonp (url+queryParms,opt)
    }
}


