<template>
    <div class="like-box">
        <div class="l_title">
            <p><span>&nbsp;</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>&nbsp;</span></p>
        </div>
        <div class="guess_like">
            <div class="like_item" 
            v-for="(item,i) in guessLike" :key="'like'+i" 
            @click="seadItem(item)">
                <div class="like_img">
                    <img :src="item.img" alt="">
                </div>
                <div class="like_title">{{ item.title }}</div>
                <div class="like_res">
                    <div class="like_price"> {{ item.price }}</div>
                    <div class="like_cfav" >
                        {{ item.cfav }}
                        <i class="iconfont icon-shoucang" @click="showSc()"></i> 
                    </div>
                </div> 
            </div>
            <div class="div"></div>
        </div>
    </div>
</template>
    
<script>
export default {
  props: {
    guessLike: { type: Array, defaute: [] }
  },
  data() {
    return {};
  },
  components: {},
  methods: {
    seadItem(items) {
      delete items.props;
      console.log(items);
      this.$router.push({ path: "/product", query: { items } });
    }
  }
};
</script>
    
<style lang="less">
@import url("../less/less-var");
.like {
  &-box {
    margin-bottom: 10px;
    > .l_title {
      text-align: center;
      font-size: 32px;
      margin-top: 30px;
      margin-bottom: 30px;
    }
    > .guess_like {
      display: flex;
      flex-flow: wrap;
      justify-content: space-around;
      > .div {
        width: 45%;
      }
      > .like_item {
        width: 45%;
        height: 615px;
        > .like_img {
          width: 100%;
          height: 464px;
          > img {
            height: 100%;
            width: 100%;
            border-radius: 5px;
          }
        }
        > .like_title {
          width: 348px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          font-size:26px;
          text-indent: 1em;
          margin-top: 10px;
          position: relative;
          line-height: 35px;
          max-height: 70px;
          overflow: hidden;
        }
        .like_title::after {
          content: "...";
          position: absolute;
          bottom: 0;
          right: 0;
          padding-left: 40px;
          background: -webkit-linear-gradient(left, transparent, #fff 55%);
          background: -o-linear-gradient(right, transparent, #fff 55%);
          background: -moz-linear-gradient(right, transparent, #fff 55%);
          background: linear-gradient(to right, transparent, #fff 55%);
        }
        > .like_res {
          display: flex;
          justify-content: space-between;
          margin-top: 10px;
          > .like_price {
            font-size: 28px;
            font-weight: bold;
          }
          > .like_cfav {
            font-size: 28px;
            margin-right: 13px;
          }
        }
      }
    }
  }
}
</style>


