import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '../pages/HomePage'
import TypesPage from "../pages/TypesPage"
import CartPage from '../pages/CartPage'
import MePage from '../pages/MePage'
import SearchGood from '../components/SearchGood'
import ProductDetails from '../components/ProductDetails'

Vue.use(Router)

export default new Router({

  linkActiveClass:"active",
  linkExactActiveClass:"exactActive",

  mode:"history",

  routes: [
    {path:"/",redirect:"/home"},
    {path: '/home',name: 'home',component:HomePage},
    {path: '/types',name: 'types',component:TypesPage},
    {path: '/cart',name: 'cart',component:CartPage},
    {path: '/me',name: 'me',component:MePage},
    {path:'/search',name:"search",component:SearchGood},
    {path:'/product',name:"product",component:ProductDetails},
  ]
})



